@keyframes shine {
  to {
    background-position: 200% center; } }

@keyframes heartbeat {
  0% {
    transform: scale(1); }
  20% {
    transform: scale(1.25) translateX(0) translateY(0); }
  40% {
    transform: scale(1.5) translateX(0) translateY(0); } }

@font-face {
