*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

.header {
  min-height: 74px; }
  .header__wrapper {
    align-items: center;
    display: flex;
    justify-content: space-between;
    padding: 25px 0; }
  .header__logo {
    align-items: center;
    display: flex; }
  .header__logo-icon {
    overflow: inherit; }
  .header__logo--heartbeat {
    transform: scale(1.2); }

.sandwich {
  align-items: center;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
  display: flex;
  height: 30px;
  justify-content: center;
  position: relative;
  padding: 0;
  transition: all ease-out 0.7s;
  width: 30px;
  z-index: 10; }
  @media screen and (min-width: 1024px) {
    .sandwich {
      display: none; } }
  .sandwich__inner {
    height: 18px;
    position: relative;
    width: 100%; }
  .sandwich__line {
    background-color: #2c39f2;
    display: block;
    height: 2px;
    left: 0;
    position: absolute;
    transition: all ease-out 0.3s;
    width: 100%; }
    .sandwich__line--top {
      top: 0; }
    .sandwich__line--middle {
      top: 8px; }
    .sandwich__line--bottom {
      top: 16px; }

.sandwich--active .sandwich__line--top {
  top: 7px;
  transform: rotate(45deg); }

.sandwich--active .sandwich__line--middle {
  transform: translateX(-10px);
  opacity: 0; }

.sandwich--active .sandwich__line--bottom {
  top: 7px;
  transform: rotate(-45deg); }

.main-nav {
  background-color: #ffffff;
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 74px;
  opacity: 0;
  pointer-events: none;
  transition: all ease-out 0.3s;
  visibility: hidden; }
  .main-nav--active {
    opacity: 1;
    pointer-events: all;
    visibility: visible; }
  .main-nav__list {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    margin: 0;
    padding: 0;
    transform: translateY(-37px); }
  .main-nav__item {
    list-style: none; }
    .main-nav__item:not(:last-child) {
      margin: 0 0 30px 0; }
  .main-nav__link {
    color: #0b0b0b;
    font-size: 28px;
    font-weight: 500;
    line-height: 28px;
    position: relative;
    transition: opacity ease-out 0.15s; }
    .main-nav__link--test {
      animation: shine 1s linear infinite;
      background: linear-gradient(to left, #e72e8b 0%, #1f3e92 40%, #1f3e92 60%, #e72e8b 80%);
      background-size: 200% auto;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .main-nav__link:hover {
      opacity: 0.7; }
  .main-nav__link-icon {
    position: absolute;
    top: 0;
    right: -15px; }

